}
@media (min-width: 1024px) {
  .intro--main {
    margin-bottom: 105px;
    background-image: url("../img/bg/intro-bg-main-desktop.jpg");
  }
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .intro--main {
    background-image: url("../img/bg/intro-bg-main-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .intro--main {
    margin-top: -6px;
    margin-bottom: 55px;
    background-image: url("../img/bg/intro-bg-main-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .intro--main {
    background-image: url("../img/bg/intro-bg-main-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .intro--main {
    margin-bottom: 45px;
    background-image: url("../img/bg/intro-bg-main-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .intro--main {
    background-image: url("../img/bg/intro-bg-main-mobile@2x.jpg");
  }
}
.intro--catalog {
  height: 261px;
}
@media (min-width: 1024px) {
  .intro--catalog {
    margin-bottom: 80px;
    background-image: url("../img/bg/intro-bg-catalog-desktop.jpg");
  }
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .intro--catalog {
    background-image: url("../img/bg/intro-bg-catalog-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .intro--catalog {
    margin-top: -6px;
    margin-bottom: 50px;
    background-image: url("../img/bg/intro-bg-catalog-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .intro--catalog {
    background-image: url("../img/bg/intro-bg-catalog-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .intro--catalog {
    margin-bottom: 40px;
    background-image: url("../img/bg/intro-bg-catalog-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .intro--catalog {
    background-image: url("../img/bg/intro-bg-catalog-mobile@2x.jpg");
  }
}
.intro h2 {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}
.intro p {
  margin: 0;
  padding: 0;
}

.intro__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.intro__box {
  font-family: "cormorantgaramond", "Arial", sans-serif;
  line-height: 130%;
  text-align: center;
  color: #ffffff;
}
.intro__box--primary {
  font-weight: 700;
  text-transform: uppercase;
}
.intro__box--prime-main {
  font-size: 4.5rem;
  letter-spacing: 0.03em;
}
@media (min-width: 1024px) {
  .intro__box--prime-main {
    transform: translate(0, 4px);
  }
}
@media (max-width: 1023px) {
  .intro__box--prime-main {
    transform: translate(-9px, 3px);
  }
}
@media (max-width: 767px) {
  .intro__box--prime-main {
    font-size: 3.8rem;
    transform: translate(0, 3px);
  }
}
.intro__box--prime-catalog {
  font-size: 3.4rem;
  transform: translate(0, -4px);
}
.intro__box--auxiliary {
  font-size: 3.8rem;
  font-weight: 400;
  font-style: italic;
}
.intro__box--aux-main {
  text-transform: lowercase;
}
@media (max-width: 1023px) {
  .intro__box--aux-main {
    transform: translate(-9px, -3px);
  }
}
@media (max-width: 767px) {
  .intro__box--aux-main {
    font-size: 3.4rem;
    transform: translate(0, -6px);
  }
}
.intro__box--aux-catalog {
  font-size: 2.2rem;
  font-weight: 500;
}
@media (min-width: 1024px) {
  .intro__box--aux-catalog {
    transform: translate(2px, 0);
  }
}
@media (max-width: 1023px) {
  .intro__box--aux-catalog {
    width: 488px;
  }
}
@media (max-width: 767px) {
  .intro__box--aux-catalog {
    line-height: 120%;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .intro__box--catalog {
    width: 248px;
  }
}

@media (min-width: 1024px) {
  .new-goods {
    margin-bottom: 97px;
  }
}
@media (max-width: 1023px) {
  .new-goods {
    margin-bottom: 49px;
  }
}
@media (max-width: 767px) {
  .new-goods {
    margin-bottom: 40px;
  }
}
.new-goods h2 {
  letter-spacing: normal;
