.login__singup-link:hover, .login__singup-link:focus {
  border-color: inherit;
}
.login__singup-link:active {
  opacity: 0.5;
}

.login__cross {
  --icon-color: #1d1613;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-13px, 20px);
  transform-origin: 50% 44%;
  transition: transform 0.5s ease;
  z-index: 1;
}
@media (max-width: 767px) {
  .login__cross {
    transform: translate(-9px, 14px);
  }
}
@media (max-width: 1023px) and (min-width: 768px) {
  .login__cross svg {
    width: 20px;
    height: 20px;
  }
}
.login__cross:hover {
  transform: translate(-13px, 20px) rotate(180deg);
}
@media (max-width: 767px) {
  .login__cross:hover {
    transform: translate(-9px, 14px) rotate(180deg);
  }
}
.login__cross:focus {
  --icon-color: rgba(255, 166, 0, 0.966);
}
.login__cross:active {
  --icon-color: #ffffff;
}

.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .login-page {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media (max-width: 1023px) {
  .login-page {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .login-page {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
.login-page h1 {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}
.login-page .login {
  position: static;
  transform: translate(0, 0);
}

.hidden-entity {
  display: none;
}

@media (max-width: 1023px) {
  .hidden-before-desktop {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hidden-on-desktop {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-before-tablet {
    display: none;
  }
}

@media (min-width: 768px) {
  .hidden-after-mobile {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-on-mobile {
    display: none;
  }
}